export const throttle = (fn: Function, ms: number) => {
    //  
    let ready = true;
    const throttled = (...args: any[]) => {
      //      `false`
      if (!ready) return;
      //  
      fn(...args);
      //  
      ready = false;
      const timer = setTimeout(() => {
        //      
        ready = true;
        //  
        clearTimeout(timer);
      }, ms);
    };
    return throttled;
  };